body {
  background-image: url("b.cd93ef5d.jpg");
  background-size: 20%;
  overflow: hidden;
}

.contacts__box {
  width: 60%;
  height: 60vh;
  background-color: #d4b1b1b8;
  border-radius: 30px;
  margin: 15vh auto 0;
  display: flex;
}

.contacts__box > ul {
  width: 50%;
}

.m__t {
  background-color: #51ff003a;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.m__t svg {
  width: 30px;
  height: 30px;
  margin-right: 25px;
}

@media screen and (min-width: 767.5px) {
  .m__t svg {
    width: 60px;
    height: 60px;
  }
}

.m__t a {
  color: #34495e;
  background-color: red;
  margin: 20px;
}

@media screen and (min-width: 767.5px) {
  .m__t a {
    font-size: 30px;
  }
}

.messengers {
  background-color: #ffe4c43e;
}

